var Airtable = require('airtable');
var base = new Airtable({apiKey: 'keyiBvXqBMcs7Lf9F'}).base('appArroZ7O0hialr4');

export class AirtableAPI {
    
    constructor() {   
    }

    getAll(basename, limit=undefined) {
        if(limit != undefined) {
            return base(basename).select({
                maxRecords: parseInt(limit),
                sort: [{field: "judul", direction: "asc"}]
            }).all(); 
        }

        return base(basename).select({
            sort: [{field: "judul", direction: "asc"}]
        }).all(); 
    }

    getOne(basename, filter) {
        return base(basename).select({
            filterByFormula: filter
        }).all();
    }
}